/* General Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    background-image: url('../image/Background.jpg'); /* Ensure this path is correct */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
}

/* Cookie Consent Banner */
.cookie-banner {
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    padding: 15px;
    border-radius: 10px;
    z-index: 1000;
    display: none; /* Initially hidden */
    animation: fadeIn 1s ease-out;
}

.cookie-banner-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cookie-banner p {
    font-size: 14px;
    margin-bottom: 10px;
    text-align: center;
}

.cookie-categories {
    margin-bottom: 20px;
    text-align: left;
    width: 100%;
}

.cookie-category {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 10px;
}

.cookie-category label {
    font-size: 14px;
    color: #ddd;
    margin-bottom: 0;
}

.cookie-category input {
    transform: scale(1.2);
}

.cookie-buttons {
    display: flex;
    gap: 10px;
}

.cookie-buttons button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.cookie-buttons .accept-button {
    background-color: #4CAF50;
    color: white;
}

.cookie-buttons .reject-button {
    background-color: #f44336;
    color: white;
}

.cookie-buttons .customize-button {
    background-color: #2196F3;
    color: white;
}

.cookie-buttons button:hover {
    opacity: 0.8;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        top: -50px;
    }
    100% {
        opacity: 1;
        top: 0;
    }
}

/* Login Page Styles */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    padding: 30px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.login-card {
    width: 100%;
}

.card-header h2 {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
    color: #fff;
}

.input-group {
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
}

.input-group label {
    font-size: 14px;
    color: #ccc;
    margin-bottom: 5px;
}

.input-group input {
    padding: 12px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: #333;
    color: white;
    outline: none;
}

.input-group input::placeholder {
    color: #bbb;
}

.login-button {
    background-color: #4CAF50;
    color: white;
    padding: 12px;
    width: 100%;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-bottom: 10px;
    transition: background-color 0.3s;
}

.login-button:hover {
    background-color: #45a049;
}

.forgot-password {
    color: #bbb;
    text-align: center;
    display: block;
    margin-top: 10px;
    text-decoration: none;
}

.forgot-password:hover {
    color: #fff;
}

.social-login {
    text-align: center;
    margin-top: 20px;
}

.social-login p {
    font-size: 14px;
    color: #ccc;
    margin-bottom: 15px;
}

.social-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 20px;
    font-size: 16px;
    background-color: transparent;
    border: 1px solid #ccc;
    color: #ccc;
    cursor: pointer;
    width: 100%;
    border-radius: 5px;
    margin-bottom: 10px;
    transition: background-color 0.3s;
}

.social-button:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

.social-button img {
    width: 20px;
    margin-right: 10px;
}

/* Responsive Design */
@media (max-width: 768px) {
    .login-container {
        width: 90%;
    }

    .cookie-banner {
        width: 90%;
    }
}
